import { FoldableHeadingData, Heading } from '@ali/4ever-mo';
import { Block } from '@ali/4ever-cangjie';
import { HeadingGroupPlugin } from '@ali/4ever-bamboo';

const {
  condition,
} = HeadingGroupPlugin.utils;

const {
  Heading1,
  Heading2,
  Heading3,
  Heading4,
  Heading5,
  Heading6,
} = Heading;

export const headingGroupList: Array<{
  /**
   * 聚合标题
   */
  when: (node: any) => node is Block<FoldableHeadingData>;
  /**
   * 聚合截止
   */
  until: (node: any) => boolean;
  /**
   * 标题类型
   */
  type: string;
}> = [
  {
    when: Heading1.isHeading1,
    until: Heading1.isHeading1,
    type: Heading1.TYPE,
  },
  {
    when: Heading2.isHeading2,
    until: condition(
      Heading1.isHeading1,
      Heading2.isHeading2,
    ).some,
    type: Heading2.TYPE,
  },
  {
    when: Heading3.isHeading3,
    until: condition(
      Heading1.isHeading1,
      Heading2.isHeading2,
      Heading3.isHeading3,
    ).some,
    type: Heading3.TYPE,
  },
  {
    when: Heading4.isHeading4,
    until: condition(
      Heading1.isHeading1,
      Heading2.isHeading2,
      Heading3.isHeading3,
      Heading4.isHeading4,
    ).some,
    type: Heading4.TYPE,
  },
  {
    when: Heading5.isHeading5,
    until: condition(
      Heading1.isHeading1,
      Heading2.isHeading2,
      Heading3.isHeading3,
      Heading4.isHeading4,
      Heading5.isHeading5,
    ).some,
    type: Heading5.TYPE,
  },
  {
    when: Heading6.isHeading6,
    until: condition(
      Heading1.isHeading1,
      Heading2.isHeading2,
      Heading3.isHeading3,
      Heading4.isHeading4,
      Heading5.isHeading5,
      Heading6.isHeading6,
    ).some,
    type: Heading6.TYPE,
  },
];
